<template>
  <div id="indexPool">
    <div class="wind-block qq-block--cont">
      <div class="title">
        冻结wind token
        <router-link class="flex-center t-right" :to="{name: 'ListFreeze'}">
          <img src="@/assets/images/record@2x.png" alt="" :srcset="`${require('@/assets/images/record@3x.png')} 3x`" class="icon-record">
        </router-link>
      </div>
      <van-field
        class="mb15"
        type="number"
        placeholder="请输入冻结wind token 的数量">
      </van-field>
      <div class="cell-sellect flex-between" @click="showDayPicker = true">
        {{dayValue}}
        <img src="@/assets/images/arrow_b@2x.png" alt="" :srcset="`${require('@/assets/images/arrow_b@3x.png')} 3x`" class="icon_arrow">
      </div>
      <div class="cell-tip flex-between">
        <div>已冻结：<span class="c-1FC7D4">526.00wind token</span></div>
      </div>
      <div class="cell-button">
        <van-button type="primary" size="large">确定</van-button>
      </div>
    </div>
    <div class="dividend-block">
      <div class="tit">累积分红</div>
      <router-link class="flex-center t-right" :to="{name: 'ListPool'}">
        <img src="@/assets/images/record@2x.png" alt="" :srcset="`${require('@/assets/images/record@3x.png')} 3x`" class="icon-record">
      </router-link>
      <div class="num">0 BUSD</div>
    </div>
    <div class="qq-block--cont">
      <div class="cell-tit flex-between">
        做市商资金池余额(BUSD)
        <span class="cell-tit--right">记录</span>
      </div>
      <div class="cell-balance">
        <div class="num">{{marketToTalMoney > curCycleIncome ? curCycleIncome : marketToTalMoney}}</div>
        <div class="flex-between">
          <div class="info-item flex-item">
            <p class="tit">做市商资金池当日盈利</p>
            <p class="num c-2FBBA1">{{curCycleIncome - marketToTalMoney}}</p>
          </div>
          <div class="info-item flex-item"></div>
        </div>
      </div>
      <div class="cell-tit">委托数量</div>
      <van-field
        type="number"
        v-model="amount"
        placeholder="请输入您的委托数量">
        <template #extra><span class="input-right">BUSD</span></template>
      </van-field>
      <div class="cell-tip flex-between">
        <span>用户做市商总额：{{userMarket}}BUSD</span>
        <span class="cell-tip--link" @click="showModal = true">赎回</span>
      </div>
      <div class="cell-button">
        <van-button type="primary" size="large" v-if="!address" @click="initWallet">连接钱包</van-button>
        <van-button type="primary" size="large" v-else-if="!Number(tokenAllowance)" @click="approve">授权BUSD</van-button>
        <van-button type="primary" :block="true" size="large" v-else @click="addpoolMoney">确定</van-button>
      </div>
    </div>
    <!-- 冻结天数 -->
    <van-popup v-model:show="showDayPicker" position="bottom">
      <van-picker
        :columns="day"
        @confirm="onDayConfirm"
        @cancel="showDayPicker = false"
      />
    </van-popup>
    <!-- 赎回 -->
    <van-popup v-model:show="showModal" :style="{ background: 'transparent' }">
      <div class="modal-box">
        <div class="modal-title">赎回</div>
        <div class="modal-close flex-center" @click="showModal = false"><img src="@/assets/images/close@2x.png" alt="" :srcset="`${require('@/assets/images/close@3x.png')} 3x`" class="icon_arrow"></div>
        <div class="modal-cont">
          <div class="modal-tip flex-between">
            <div>赎回数量</div>
            <div class="c-1FC7D4">上限：{{userMarket}}</div>
          </div>
          <van-field
            type="number"
            v-model="wAmount"
            placeholder="请输入赎回wind token">
          </van-field>
        </div>
        <div class="modal-btn">
          <van-button type="primary" size="large" v-if="!address" @click="initWallet">连接钱包</van-button>
          <van-button type="primary" size="large" v-else @click="markerRedeem">确定</van-button>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { reactive, toRefs, computed, watch} from "vue";
import BSNAbi from "@/utils/BSNAbi";
import tokenAbi from "@/utils/abi";
import BSNDataAbi from "@/utils/BSNDataAbi";
import { MaxUint256 } from "@ethersproject/constants";
import config from "@/utils/config";
import web3Data from "@/utils/initWeb3";
import {useStore} from "vuex";
import Web3 from "web3";
export default {
  setup() {
    const store = useStore()
    const data = reactive({
      day: [
        '3天',
        '4天'
      ],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      BsnContract: null,
      amount: '',
      BusdContract: null,
      tokenAllowance: 0,
      marketToTalMoney: 0,
      userMarket: 0,
      wAmount: '',
      curCycleIncome: 0,
      showModal: false,
      showDayPicker: false,
      dayValue: '3天',
      onDayConfirm: (value) => {
        data.dayValue = value;
        data.showDayPicker = false;
      },
      initWallet: () => {
         web3Data.initWeb3()
      },
      initContract: () => {
        data.BsnContract = new data.web3.eth.Contract(BSNAbi, config.BsnAddress)
        data.BusdContract = new data.web3.eth.Contract(tokenAbi, config.BusdAddress)
        data.BsnDataContract = new data.web3.eth.Contract(BSNDataAbi, config.BsnDataAddress)
        data.initData()
      },
       initData: () => {
         data.getCurCycleIncome()
         data.getUserInfo()
         data.getTokenAllowance()
      },
      getCurCycleIncome: async () => {
        let decimals = await data.BusdContract.methods.decimals().call();
        let cur =  await data.BsnContract.methods.getCurCycleIncome().call();
        let market = await data.BsnDataContract.methods.getMarketToTalMoney().call();
        data.curCycleIncome = Number(cur / Math.pow(10, decimals)).toFixed(2)
        data.marketToTalMoney = Number(market / Math.pow(10, decimals)).toFixed(2)
        console.log(data.marketToTalMoney, data.curCycleIncome)

      },
      getUserInfo: async () => {
        let  userInfo = await data.BsnDataContract.methods.getUserInfo(data.address).call();
        let decimals = await data.BusdContract.methods.decimals().call();
        data.userMarket = Number(userInfo.marketProvide / Math.pow(10, decimals)).toFixed(2);
      },
      addpoolMoney: async () => {
        if (data.amount < 100) {
          Toast('数量必须大于等于100')
          return
        }
        let decimals = await data.BusdContract.methods.decimals().call();
        let BN = Web3.utils.BN;
        let amount = new BN(data.amount * Math.pow(10, decimals)).toString();
        let result = await data.BsnContract.methods.addMarketPool(amount).send({from: data.address});
      },
      approve: async () => {
        let result = await data.BusdContract.methods.approve(config.BsnAddress, MaxUint256).send({from: data.address});
        data.getTokenAllowance()
      },
      getTokenAllowance: async () => {
        let result = await data.BusdContract.methods.allowance(data.address, config.BsnAddress).call();
        data.tokenAllowance = result;
        let getCurCycleData = await data.BsnDataContract.methods.getCurCycleData().call();
        console.log('1111', getCurCycleData);
      },
      markerRedeem: async () => {
        if (data.wAmount <= 0) {
          Toast('数量必须大于0')
          return
        }
        let decimals = await data.BusdContract.methods.decimals().call();
        let BN = Web3.utils.BN;
        let amount = new BN(data.wAmount * Math.pow(10, decimals)).toString();
        let result = await data.BsnContract.methods.markerRedeem(amount).send({from: data.address});
      },
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.qq-block--cont {
  margin: 15px !important;
}
.mb15 {
  margin-bottom: 15px;
}
.t-right {
  width: 52px;
  height: 52px;
  position: absolute;
  top: 9px;
  right: 0;
  .icon-record {
    width: 20px;
    height: 20px;
  }
}
.wind-block {
  position: relative;
  .title {
    font-size: 21px;
    line-height: 40px;
    color: #8F80BA;
    font-weight: bold;
    text-align: center;
    margin-bottom: 13px;
  }
}
.dividend-block {
  position: relative;
  background: #fff;
  margin: 15px;
  border-radius: 25px;
  font-weight: bold;
  font-size: 16px;
  color: #8F80BA;
  padding: 23px 16px 15px;
  .num {
    font-size: 25px;
    color: #452A7A;
  }
}
.modal-tip {
  font-size: 14px;
  color: #8F80BA;
  margin-bottom: 10px;
}
.modal-box .modal-btn {
  margin-top: 30px !important;
}
</style>