<template>
  <div id="indexOption">
    <div class="qq-chart">
      <div class="flex-middle coin-title">BETH<img src="@/assets/images/arrow_b@2x.png" alt="" :srcset="`${require('@/assets/images/arrow_b@3x.png')} 3x`" class="icon_arrow"></div>
      <div class="coin-num">
        <div class="flex-middle">
          <span class="price">{{price}}BUSDT</span>
          <!-- <span class="rate">+1.23%</span> -->
        </div>
        <!-- <div class="s-price">$2900.00</div> -->
      </div>
      <div class="coin-address">
        地址 {{address}}
      </div>
      <!-- <div class="chart-box border-1px-t">
        <div class="chart-btns flex">
          <span class="btn-item active">24H</span>
          <span class="btn-item">近一周</span>
          <span class="btn-item">近一月</span>
          <span class="btn-item">近一年</span>
          <span class="btn-item">全部</span>
        </div>
      </div> -->
    </div>
    <van-tabs @click="blockClick" v-model:active="blockName" type="card" class="qq-block--tab">
      <van-tab title="期权板块" name="qq">
        <div class="qq-block--cont">
          <div class="cell-tit flex-between">
            请选择委托周期
            <router-link class="cell-tit--right" :to="{name: 'List'}">历史记录</router-link>
          </div>
          <div class="cell-sellect flex-between" @click="showColumnsPicker = true">
            {{columnsValue}}
            <img src="@/assets/images/arrow_b@2x.png" alt="" :srcset="`${require('@/assets/images/arrow_b@3x.png')} 3x`" class="icon_arrow">
          </div>
          <div class="cell-tit">请输入委托数量</div>
          <div class="cell-number flex-wrap">
            <div class="item" :class="{'active': amount == '100'}" @click="changeAmount('100')"><span>100BUSD</span></div>
            <div class="item" :class="{'active': amount == '200'}" @click="changeAmount('200')"><span>200BUSD</span></div>
            <div class="item" :class="{'active': amount == '1000'}" @click="changeAmount('1000')"><span>1000BUSD</span></div>
          </div>
          <van-field
            class="mb15"
            type="number"
            v-model="amount"
            placeholder="请输入您的委托数量">
            <template #extra><span class="input-right">BUSD</span></template>
          </van-field>
          <div class="cell-tit">请选择委托方向：<span v-if="wtType === 1" class="c-2FBBA1">看涨</span><span v-if="wtType === 0" class="c-D4566C">看跌</span></div>
          <div class="cell-type flex" v-if="address">
            <div class="btn flex-item" :class="{'active-2FBBA1': wtType === 1}" @click="changeWtType(1)">看涨</div>
            <div class="btn flex-item" :class="{'active-D4566C': wtType === 0}" @click="changeWtType(0)">看跌</div>
          </div>
          <div class="cell-type flex" v-else>
            <div class="btn flex-item active-D4566C"  @click="initWallet">连接钱包</div>
          </div>
          <div class="cell-msg">注：一个地址在一个区块内仅接受<span class="c-1FC7D4">一次转账</span>，且单笔转账不能超过<span class="c-1FC7D4">100000BUSD</span>，超出将按照上限记录下单金额，多余部分在下一区块退回。</div>
          <!-- <div class="cell-button">
            <van-button type="primary" size="large" @click="showModal = true">确定</van-button>
          </div> -->
        </div>
      </van-tab>
      <van-tab title="流水记录" class="ls">
        <div class="qq-block--record">
          <van-tabs class="qq-block--recordtab" line-width="25px">
            <van-tab title="已开奖"></van-tab>
            <van-tab title="未开奖"></van-tab>
          </van-tabs>
          <div>
            <div class="record-item">
              <div class="flex-between title">
                <div class="flex-item">0Xjskdjs***52hm35k9</div>
                <div class="status">正确</div>
                <!-- <div class="status c-D4566C">错误</div> -->
              </div>
              <div class="flex">
                <div class="flex-item">委托方向：<span class="c-2FBBA1">看涨</span></div>
                <div class="flex-item text-r">委托数量：100BUSD</div>
              </div>
              <div class="flex">
                <div class="flex-item">委托本金：扣除</div>
                <div class="flex-item text-r">区块：0001-0021</div>
              </div>
              <div class="flex">
                <div class="flex-item">额外获得：<span class="c-2FBBA1">300BSC</span></div>
              </div>
            </div>
            <div class="record-item">
              <div class="flex-between title">
                <div class="flex-item">0Xjskdjs***52hm35k9</div>
                <div class="status c-D4566C">错误</div>
              </div>
              <div class="flex">
                <div class="flex-item">委托方向：<span class="c-D4566C">看跌</span></div>
                <div class="flex-item text-r">委托数量：100BUSD</div>
              </div>
              <div class="flex">
                <div class="flex-item">委托本金：扣除</div>
                <div class="flex-item text-r">区块：0001-0021</div>
              </div>
              <div class="flex">
                <div class="flex-item">额外获得：<span class="c-2FBBA1">300BSC</span></div>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
  <!-- 选择委托周期 -->
  <van-popup v-model:show="showColumnsPicker" position="bottom">
    <van-picker
      :columns="columns"
      @confirm="onColumnsConfirm"
      @cancel="showColumnsPicker = false"
    />
  </van-popup>
  <!-- 确认委托 -->
  <van-popup v-model:show="showModal" :style="{ background: 'transparent' }">
    <div class="modal-box">
      <div class="modal-title">确认委托</div>
      <div class="modal-close flex-center" @click="showModal = false"><img src="@/assets/images/close@2x.png" alt="" :srcset="`${require('@/assets/images/close@3x.png')} 3x`" class="icon_arrow"></div>
      <div class="modal-cont">
        <div class="row flex">
          <div class="label">当前BETH起始价格：</div>
          <div class="cont text-r flex-item">{{price}}BUSD</div>
        </div>
        <div class="row flex">
          <div class="label">委托周期：</div>
          <div class="cont text-r flex-item">{{curMarketType.duration}}个块({{curMarketType.minute}}分钟)</div>
        </div>
        <div class="row flex">
          <div class="label">委托方向：</div>
          <div class="cont text-r flex-item">
            <span class="c-2FBBA1" v-if="wtType === 1">看涨</span>
            <span class="c-D4566C" v-if="wtType === 0">看跌</span>
          </div>
        </div>
        <div class="row flex">
          <div class="label">委托金额：</div>
          <div class="cont text-r flex-item">{{amount}}BUSD</div>
        </div>
      </div>
      <div class="modal-btn" v-if="Number(tokenAllowance)">
        <van-button type="primary" @click="invest" size="large">确定</van-button>
      </div>
      <div class="modal-btn" v-else>
        <van-button type="primary" @click="approve" size="large">授权BUSD</van-button>
      </div>
    </div>
  </van-popup>
</template>
<script>
import { Toast } from 'vant';
import { reactive, toRefs, computed, watch} from "vue";
import BSNAbi from "@/utils/BSNAbi";
import tokenAbi from "@/utils/abi";
import { MaxUint256 } from "@ethersproject/constants";
import config from "@/utils/config";
import web3Data from "@/utils/initWeb3";
import {useStore} from "vuex";
import Web3 from "web3";
export default {
  setup() {
    const store = useStore()
    const data = reactive({
      showModal: false,
      price: '',
      amount: '',
      blockName: 'qq',
      columns: [],
      marketType: [],
      curMarketType: null,
      showColumnsPicker: false,
      columnsValue: '请选择',
      wtType: 1,

      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      BsnContract: null,
      BusdContract: null,
      tokenAllowance: 0,
      changeWtType: (type) => {
        data.wtType = type
        if (!data.curMarketType) {
          Toast('请选择委托周期')
          return
        }
        if (!data.amount) {
          Toast('请输入投资金额')
          return
        }
        data.showModal = true
      },
      blockClick: (name, title) => Toast(title),
      changeAmount: (val) => {
        data.amount = val;
      },
      onColumnsConfirm: (value, index) => {
        data.columnsValue = value;
        data.showColumnsPicker = false;
        data.curMarketType = data.marketType[index]
      },
       initWallet: () => {
         web3Data.initWeb3()
       },
      initContract: () => {
        data.BsnContract = new data.web3.eth.Contract(BSNAbi, config.BsnAddress)
        data.BusdContract = new data.web3.eth.Contract(tokenAbi, config.BusdAddress)
        data.initData()
      },
      initData: () => {
        data.getMarketTypeIds()
        data.getEthUsdt()
        data.getTokenAllowance()
      },
      getTokenAllowance: async () => {
        let result = await data.BusdContract.methods.allowance(data.address, config.BsnAddress).call();
        data.tokenAllowance = result
      },
      getMarketTypeIds: async() => {
        console.log(data.BsnContract)
        let arr = []
        let comlArr = []
        let ids = await data.BsnContract.methods.getMarketTypeIds().call()
        console.log(ids)
        ids.forEach(async item => {
          let result = await data.BsnContract.methods.marketTypes(item).call()
          let params = {
            minute: result.minute,
            duration: result.duration,
            maxAmount: result.maxAmount,
            minAmount: result.minAmount,
          }
          arr.push(params)
          comlArr.push(`${result.duration}个块(${result.minute}分钟)`)
        });
        data.marketType = arr
        data.columns = comlArr
      },
      getEthUsdt: async () => {
        let result = await data.BsnContract.methods.getEthUsd().call();
        let price = result / Math.pow(10, 10);
        data.price = Number(price).toFixed(4);
      },
      approve: async () => {
        let result = await data.BusdContract.methods.approve(config.BsnAddress, MaxUint256).send({from: data.address});
        data.getTokenAllowance()
      },
      invest: async () => {
        let decimals = await data.BusdContract.methods.decimals().call();
        let BN = Web3.utils.BN;
        console.log(decimals)
        let amount = new BN(data.amount * Math.pow(10, decimals)).toString();
        let result = await data.BsnContract.methods.invest(data.wtType, data.curMarketType.minute, amount).send({from: data.address});
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
#indexOption {
  .qq-block--recordtab {
    margin-bottom: 10px;
    border-radius: 13px;
    overflow: hidden;
    .van-tabs__nav {
      background: #EEEAF4;
      .van-tab {
        color: #AFA4CC;
      }
      .van-tab--active {
        color: #1FC7D4;
      }
      .van-tabs__line {
        bottom: 18px;
        background: #1FC7D4;
      }
    }
  }
}
</style>
<style lang="less" scoped>
.container {
  padding-top: 107px !important;
}
.mb15 {
  margin-bottom: 15px;
}
.qq-chart {
  margin: 20px 15px;
  background: #fff;
  border-radius: 25px;
  padding: 20px 0 10px;
  .coin-title {
    font-size: 25px;
    color: #452A7A;
    font-weight: bold;
    padding: 0 20px;
    .icon_arrow {
      width: 12px;
      height: 12px;
      margin-left: 8px;
    }
  }
  .coin-num {
    padding: 0 20px;
    .price {
      font-size: 25px;
      color: #452A7A;
      font-weight: bold;
    }
    .rate {
      margin-left: 19px;
      height: 23px;
      line-height: 23px;
      background: rgba(31, 199, 212, .2);
      border-radius: 12px;
      padding: 0 12px;
      font-size: 14px;
      color: #1FC7D4;
      font-weight: bold;
    }
    .s-price {
      font-size: 14px;
      color: #8F80BA;
    }
  }
  .coin-address {
    margin: 10px 15px 21px;
    background: rgba(238, 234, 244, .4);
    padding: 8px 12px;
    border-radius: 17px;
    color: #8F80BA;
    font-size: 13px;
    font-weight: bold;
    word-break: break-all
  }
  .chart-box {
    padding: 10px 15px;
    .chart-btns {
      overflow: auto;
      .btn-item {
        font-size: 12px;
        font-weight: bold;
        color: #1FC7D4;
        line-height: 20px;
        padding: 0 13px;
        margin: 0 7px 10px 0;
        white-space: nowrap;
        &:last-child {
          margin: 0;
        }
        &.active {
          color: #fff;
          background: #1FC7D4;
          border-radius: 10px;
        }
      }
    }
  }
}
.qq-block--record {
  background: #fff;
  border-radius: 25px;
  margin: 20px 15px;
  padding: 20px 15px;
  .record-item + .record-item {
    margin-top: 15px;
  }
  .record-item {
    background: #EEEAF4;
    border-radius: 13px;
    padding: 0 12px 8px;
    font-size: 12px;
    color: #8F80BA;
    line-height: 20px;
    word-break: break-all;
    .title {
      color: #452A7A;
      font-size: 15px;
      font-weight: bold;
      padding: 15px 0 7px;
      word-break: break-all;
      .status {
        white-space: nowrap;
        color: #1FC7D4;
        font-size: 13px;
        margin-left: 15px;
      }
    }
  }
}
</style>